html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}

body {
  font-family: Montserrat, sans-serif;
}

.fixed-body {
  overflow: hidden;
}

.wrapper {
  position: relative;
  overflow: hidden;
}

.container-fluid {
  max-width: 2012px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: PlayfairDisplay;
  font-weight: 600;
}

img {
  object-fit: cover;
}